.project-section {
    margin-top: 100px;
    margin-bottom: 100px;
}

.project-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    /* grid-gap: 0.25rem; */
    place-items: center;
    justify-content: center;
} 

.project {
    width: 50vw;
    height: 50vw;
    display: flex;
    align-items: center;
    justify-content: center;
    /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2); */
    position: relative;
    /* border: .025px solid gray; */
    /* border-radius: 20px; */
}

.project-title {
    position: absolute;
    top: 15px;
    font-weight: bold;
    display: none;
    /* color: white; */
}

.project-desc {
    /* color: white; */
    position: absolute;
    bottom: 15px;
    text-align: center;
    display: none;
    font-size: 18px;
    width: 90%;
}

.project-wrapper img {
    width: 50%;
    height: 50%;
    object-fit: contain;

}

.project:hover img {
    /* -webkit-transform: translate3D(10px, 0, 0);    
    transform: translate3D(10px, 0, 0); */
    width: 55%;
    height: 55%;
}

.project:hover .project-title, .project:hover .project-desc {
    display: block;
}

.project-section .subtitle {
    display: none;
}

.project-section .title {
    display: none;
}

