    width: 80%;
    margin: 0 auto;
}

#about-section .title {
    width: 80%;
    margin: 0 auto;
}

#about-etc {
    text-align: right;
}

.skills {
    width: 80%;
    margin: 0 auto;
    text-align: center;
}

.skills img {
    height: 30px;
    object-fit: cover;
    margin: 2px;
}
  
.message {
    border-radius: 20px;
    padding: 8px 15px;
    display: inline-block;
}

.mine .message {
    color: white;
    /* margin-left: 25%; */
    background: -webkit-gradient(linear, left top, left bottom, from(#00D0EA), to(#0085D1));
    background: linear-gradient(to bottom, #00D0EA 0%, #0085D1 100%);
    background-attachment: fixed;
    position: relative;
}

.mine .message.last:before {
    content: "";
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: -8px;
    height: 20px;
    width: 20px;
    background: -webkit-gradient(linear, left top, left bottom, from(#00D0EA), to(#0085D1));
    background: linear-gradient(to bottom, #00D0EA 0%, #0085D1 100%);
    background-attachment: fixed;
    border-bottom-left-radius: 15px;
}

.mine .message.last:after {
    content: "";
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: -10px;
    width: 10px;
    height: 20px;
    background: white;
    border-bottom-left-radius: 10px;
}

.messages {
    width: 80vw;
    margin: 0 auto;
}

